@use "variable" as v;

html {
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-weight: 100;
  font-size: 62.5%;

  @media screen and (min-width: 460px) {
    font-family: 'M PLUS Rounded 1c', sans-serif;
  }
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

#waveCanvas {
  position: absolute;
  bottom: 0;
}

// 
html::before,
html::after,
body::before,
body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
}

// box-shadow
html {
  &::before {
    box-shadow: v.$box-shadow--star-white;
  }

  &::after { 
    box-shadow: v.$box-shadow--star-pink;
  }
}


body {
  &::before {
    box-shadow: v.$box-shadow--star-purple;
  }

  &::after {
    box-shadow: v.$box-shadow--star-green;
  }
}
