.c-anim-box, .c-anim-box--rotate-r, .c-anim-box--rotate-l, .c-anim-box--scaledown, .c-anim-box--scaleup, .c-anim-box--down, .c-anim-box--rl, .c-anim-box--lr, .c-anim-box--up {
  display: block;
  opacity: 0;
  transition: 2s;
}
.c-anim-box--up {
  transform: translate(0, 50px);
}
.c-anim-box--lr {
  transform: translate(-200px, 0);
}
.c-anim-box--rl {
  transform: translate(200px, 0);
}
.c-anim-box--down {
  transform: translate(0, -200px);
}
.c-anim-box--scaleup {
  transform: scale(0.5);
}
.c-anim-box--scaledown {
  transform: scale(1.5);
}
.c-anim-box--rotate-l {
  transform: rotate(180deg);
}
.c-anim-box--rotate-r {
  transform: rotate(-180deg);
}

.c-anim-box--up.txt.is-show {
  animation: move-up 2s both;
}
.c-anim-box--up.txt.is-show:nth-child(2) {
  animation-delay: 0.6s;
}

.c-anim-box--lr.txt.show {
  animation: move-lr 1s both;
}
.c-anim-box--lr.txt.show:nth-child(2) {
  animation-delay: 0.6s;
}

.c-anim-box--up.show {
  animation: move-up 2s both;
}

.c-anim-box--lr.show {
  animation: move-lr 1s both;
}

.c-anim-box--rl.show {
  animation: move-rl 1s both;
}

.c-anim-box--down.show {
  animation: move-down 1s both;
}

.c-anim-box--scaleup.show {
  animation: move-scaleup 1s both;
}

.c-anim-box--scaledown.show {
  animation: move-scaledown 1s both;
}

.c-anim-box--rotate-l.show {
  animation: move-rotate-l 1s both;
}

.c-anim-box--rotate-r.show {
  animation: move-rotate-r 1s both;
}

@keyframes move-up {
  0% {
    opacity: 0;
    transform: translate(0, 50px);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
@keyframes move-lr {
  0% {
    opacity: 0;
    transform: translate(-200px, 0);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
@keyframes move-rl {
  0% {
    opacity: 0;
    transform: translate(200px, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes move-down {
  0% {
    opacity: 0;
    transform: translate(0, -100px);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes move-scaleup {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes move-scaledown {
  0% {
    opacity: 0;
    transform: scale(1.5);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes move-rotate-l {
  0% {
    opacity: 0;
    transform: rotate(180deg);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes move-rotate-r {
  0% {
    opacity: 0;
    transform: rotate(-180deg);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.c-bg-square {
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 25px 45px rgba(255, 255, 0, 0.1);
  border-radius: 100%;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  animation: square 5s linear infinite;
  animation-delay: calc(-1s * var(--i));
}

@keyframes square {
  0% {
    transform: scale(1) translateY(-40px) rotateY(0);
  }
  50% {
    transform: scale(1.5) translateY(0) rotateY(0);
  }
  54% {
    transform: scale(1.5) translateY(0) rotateY(360deg);
  }
  100% {
    transform: scale(1) translateY(-40px) rotateY(360deg);
  }
}
.c-bird, .c-bird--r2, .c-bird--2, .c-bird--r1, .c-bird--1 {
  position: absolute;
  z-index: 70;
  opacity: 1;
  width: 10px;
  height: 8px;
  border: 2px solid rgba(128, 128, 128, 0.5);
  border-radius: 50%;
  border-bottom-color: transparent;
  border-right-color: transparent;
  border-top-color: transparent;
}

.c-bird--1 {
  top: 30px;
  margin-left: 3px;
  transform: rotateZ(-80deg);
  animation: move1 25s linear infinite 1.5s, flap 0.5s linear alternate infinite 1.5s;
}

.c-bird--r1 {
  top: 37px;
  margin: 0px 6px;
  transform: rotateZ(80deg);
  animation: move1 25s linear infinite 1.5s, flap 0.5s linear alternate infinite 1.5s;
}

.c-bird--2 {
  top: 50px;
  margin-left: 3px;
  transform: rotateZ(-75deg);
  animation: move2 25s linear infinite, flap 0.5s linear alternate infinite;
}

.c-bird--r2 {
  top: 57px;
  transform: rotateZ(75deg);
  margin: 0px 8px;
  animation: move2 25s linear infinite, flap 0.5s linear alternate infinite;
}

@keyframes move1 {
  0% {
    left: 100px;
  }
  100% {
    left: 100%;
  }
}
@keyframes move2 {
  0% {
    left: 100px;
  }
  100% {
    left: 100%;
  }
}
@keyframes flap {
  0% {
