  .swiper-wrapper h3 {
    font-size: 16px;
    line-height: 24px;
  }
}
.swiper-wrapper p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  color: #111;
  transform: translateY(20px);
  transition: all 0.4s ease;
  transition-delay: 0.3s;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 520px) {
  .swiper-wrapper p {
    font-size: 14px;
    line-height: 20px;
  }
}

.slider-image-wrapper {
  width: 100%;
  overflow: hidden;
}

.slider-item {
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  opacity: 0;
  cursor: grab;
}
.slider-item-content {
  width: 100%;
  padding-top: 32px;
  padding-left: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.4s;
  position: absolute;
  top: 0;
}
.slider-item-content.right {
  padding-left: 40%;
}
@media screen and (min-width: 440px) {
  .slider-item-content.right {
    padding-left: 220px;
  }
}
.slider-item-content > * {
  opacity: 0;
  transform: translateY(20px);
}
.slider-item-content p {
  color: #333;
}

.swiper-slide-active .slider-item-content > * {
  transform: translateY(0px);
  opacity: 1;
}

.slider-image {
  width: min(80vw, 420px);
  object-fit: cover;
  transition: 0.2s;
}

.swiper-pagination {
  position: absolute;
  left: 50%;
  bottom: 8px;
  transform: translatex(-50%);
  z-index: 1;
  width: auto !important;
}

.swiper-pagination-bullet {
  border-radius: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  line-height: 30px;
  font-size: 12px;
  opacity: 1;
  background: rgba(255, 185, 0, 0.3);
  display: inline-block;
  margin-right: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.swiper-pagination-bullet-active {
  background: #a3519b;
  width: 20px;
  border-radius: 10px;
}

.slider-buttons {
  display: flex;
  justify-content: space-between;
  width: min(80vw, 420px);
  top: 100%;
  padding-top: 8px;
  margin: auto;
}

.swiper-button-next {
  margin-right: 20px;
}

.swiper-button-prev {
  margin-left: 20px;
}

.swiper-button-next,
.swiper-button-prev {
  font-size: 1.5rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  color: #fff;
  position: relative;
}
.swiper-button-next:before,
.swiper-button-prev:before {
  content: "";
  position: absolute;
  background-color: #fff;
  height: 1px;
  width: 0;
  left: 0;
  bottom: -1px;
  transition: 0.2s;
}
.swiper-button-next:hover:before,
.swiper-button-prev:hover:before {
  width: 100%;
}

.p-train__container {
  position: absolute;
  top: 40.3%;
  z-index: 100;
}
.p-train__container.fixed {
  position: fixed;
  top: 2%;
}

.p-train {
  position: relative;
}
.p-train__running {
  position: absolute;
  animation: run 100s infinite linear;
}
.p-train__light {
  animation: turn 100s infinite;
  height: 0;
  width: 100px;
  box-shadow: 5px 5px 32px 7px yellow;
}
.p-train img {
  width: 150px;
  display: block;
  transform: scale(-1, 1);
}

@keyframes run {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(100vw);
  }
  100% {
    transform: translateX(-200px);
  }
}
@keyframes turn {
  0% {
    transform: scaleX(1);
  }
  49% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(-1);
  }
  99% {
    transform: scaleX(-1);
  }
  100% {
    transform: scaleX(1);
  }
}
.p-top-txt {
  position: absolute;
  top: 80%;
  left: 75%;
  transform: translateX(-50%);
  color: rgba(238, 228, 228, 0.952);
}

.sp-only {
  display: block;
}
@media (min-width: 570px) {
  .sp-only {
    display: none;
  }
}